:root {
  --default-color-text: #000;
  --accent-color: #00acdc;
}

html, body {
  margin: 0;
}

body {
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 400;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: block;
}

.nav {
  height: 60px;
}

.nav__list {
  height: 60px;
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: var(--default-color-text);
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
  display: block;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--accent-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
